@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.html {
  min-height: 100%;
}

body {
  background-color: hsl(39, 100%, 97%);
}


.container {
  min-height: 100vh; 
  display: flex;
  flex-direction: column;
}

.canvas_container {
  position: relative;
}
 
.canvas_overlay {
  position: absolute;
  top: 0;
  left: 0;
}

.floating_label_container {
  position: absolute;
  top: 0;
  left: 0;
}

.label_text {
  font-family: 'Karla', sans-serif;
  position: absolute;
}

.form-control.is-invalid{
  background-image: none;
}

.form-control {
  border: none;
}

.append-button {
  background-color: rgb(245, 143, 70);
  color: white;
}

.append-button:hover {
  background-color: rgb(247, 112, 16);
  color: white;
}

.generate-button {
  background-color: rgba(114, 185, 47, 0.74);
  color: white;
}

.generate-button:hover {
  background-color: #5eaf12c4;
  color: white;
}

.generate-button:focus {
  box-shadow: none;
}

.append-button:focus {
  box-shadow: none;
}


.header {
  background-color: navajowhite;
  font-family: 'Pangolin', cursive;
  color: rgb(255, 123, 0);
  box-shadow: 0px 0px 8px 2px rgba(127, 127, 127, .4);
  text-align: center;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 40px 0px 20px 0px;
}


.footer {
  background-color: navajowhite;
  box-shadow: 0px 0px 8px 2px rgba(127, 127, 127, .4);
  height: 120px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.btn {
  width: 100%
}

.radiusbox {
  padding: 10px;
  box-shadow: 0px 0px 8px 2px rgba(127, 127, 127, .4);
  background-color: white;
  border-radius: 10px;
}

h2 {
  font-family: 'Pangolin', cursive;
  font-weight: bold;
  font-size: 40px;
}

.row-content {
  flex: 1 1;
}

.footer-text {
  color: rgb(255, 123, 0);
  font-family: 'Karla', sans-serif;
  text-align: center;
  font-weight: bold;
  padding: 20px;
  
}







